<template>
  <div class="result-display">
    <div class="result-card">
      <div class="result-header">
        <h2 class="result-title">命理分析结果</h2>
        <button class="back-btn" @click="$emit('back')">
          <span class="back-icon">⬅️</span> 返回修改
        </button>
      </div>

      <div class="user-info">
        <h3>基本信息</h3>
        <div class="info-grid">
          <div class="info-item">
            <span class="info-label">姓名：</span>
            <span class="info-value">{{ userInfo.name }}</span>
          </div>
          <div class="info-item">
            <span class="info-label">性别：</span>
            <span class="info-value">{{ userInfo.gender === 'male' ? '男' : '女' }}</span>
          </div>
          <div class="info-item">
            <span class="info-label">出生日期：</span>
            <span class="info-value">{{ formatDate(userInfo.birthDate) }}</span>
          </div>
          <div class="info-item">
            <span class="info-label">出生时辰：</span>
            <span class="info-value">{{ userInfo.birthTime }}</span>
          </div>
          <div class="info-item">
            <span class="info-label">出生地点：</span>
            <span class="info-value">{{ userInfo.birthCity }}</span>
          </div>
          <div class="info-item">
            <span class="info-label">婚姻状况：</span>
            <span class="info-value">{{ getMaritalStatus(userInfo.maritalStatus) }}</span>
          </div>
        </div>
      </div>

      <div class="analysis-result" v-if="loading">
        <div class="loading-spinner">
          <span class="spinner-icon">🔮</span>
          <span class="loading-text">正在进行八字命理分析...</span>
        </div>
      </div>

      <div class="analysis-result" v-else>
        <div v-if="error" class="error-message">
          <span class="error-icon">⚠️</span>
          {{ error }}
        </div>
        <template v-else>
          <div class="result-section" v-for="(section, index) in parsedResult" :key="index">
            <h3 class="section-title">{{ section.title }}</h3>
            <div class="section-content" v-html="section.content"></div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import dayjs from 'dayjs'

const props = defineProps({
  userInfo: {
    type: Object,
    required: true
  }
})

const loading = ref(true)
const error = ref('')
const parsedResult = ref([])

// 格式化日期
const formatDate = (date) => {
  if (!date) return ''
  return dayjs(date).format('YYYY年MM月DD日')
}

// 获取婚姻状况文本
const getMaritalStatus = (status) => {
  const statusMap = {
    'single': '未婚',
    'married': '已婚',
    'divorced': '离异',
    'widowed': '丧偶'
  }
  return statusMap[status] || status
}

// 解析AI返回的结果
const parseResult = (text) => {
  const sections = text.split('###').filter(Boolean)
  return sections.map(section => {
    const [title, ...content] = section.trim().split('\n')
    return {
      title: title.trim(),
      content: content.join('\n')
        .replace(/\n/g, '<br>')
        .replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>')
        .replace(/- (.*)/g, '• $1')
    }
  })
}

// 调用AI接口
const fetchAnalysis = async () => {
  try {
    const response = await fetch('https://api.kksj.org/v1/chat/completions', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': 'Bearer sk-ybt88aCSRjgQ7CG3So1enrNvrRv8JnvJ4fwyW1H0135lxS06'
      },
      body: JSON.stringify({
        model: 'gpt-4o-mini',
        messages: [
          {
            role: 'system',
            content: `您是一位精通中国传统命理的顶尖大师，具备以下专业素养：

1. 命理理论基础：
   - 精通八字排盘、奇门遁甲、紫微斗数、六爻等传统命理体系
   - 深入研究《穷通宝典》《三命通会》《滴天髓》《渊海子平》等经典著作
   - 掌握子平术、星宗推演、十神定位和五行生克制化的核心理论

2. 专业分析能力：
   - 能准确解读八字命盘中的天干地支关系
   - 精通日主强弱判断、格局分析、用神取用
   - 擅长分析命主先天禀赋、性格特征和人生规律
   - 能结合大运流年进行准确的运势预测

3. 个性化分析方法：
   - 根据用户年龄特征（18岁：求学与初恋；30岁：婚姻与家庭；40岁：事业与转型；60岁：健康与养老）提供定制化建议
   - 结合现代职业发展特点进行事业指导
   - 针对不同婚姻状况提供感情分析和建议
   - 融合传统养生理论提供健康指导

4. 分析原则：
   - 遵循"趋吉避凶"的原则，引导命主扬长避短
   - 保持客观中立，不做过分夸大或诱导性判断
   - 注重实用性建议，帮助命主趋利避害
   - 分析内容应包含事业、财运、姻缘、健康等各个方面

5. 2025乙巳年专项分析：
   - 分析流年干支与命局、大运之间的相互作用
   - 考量官杀星、印星及太岁方位
   - 解析夫妻宫、桃花星、红鸾天喜联动
   - 评估婚配匹配度
   - 检查五行偏枯、体质倾向及流年病符
   - 提供节气养生建议

当前年份为2025年，请基于以下用户信息进行专业的八字命理分析。`
          },
          {
            role: 'user',
            content: `请对以下用户进行八字命理分析：
姓名：${props.userInfo.name}
性别：${props.userInfo.gender === 'male' ? '男' : '女'}
出生日期：${formatDate(props.userInfo.birthDate)}
出生时辰：${props.userInfo.birthTime}
出生城市：${props.userInfo.birthCity}
婚姻状况：${getMaritalStatus(props.userInfo.maritalStatus)}

请按照以下格式输出分析结果：

### 八字命盘
- 四柱排列（年月日时）
- 空亡标注
- 神煞分布
- 五行生克关系

### 核心命局
- 日主强弱分析
- 调候用神定位
- 格局层次判定
- 先天性格特征

### 2025乙巳年运势
- 流年干支作用
- 事业财运分析
  • 官杀星动向
  • 财星变化
  • 事业机遇点
- 感情健康预测
  • 桃花星动向
  • 健康隐患提示
  • 人际关系变化

### 大运流年
- 当前大运分析
- 流年吉凶预测
- 每月运势变化
- 重大事项选择建议

### 开运建议
- 五行调节方案
- 趋吉避凶指导
- 事业发展建议
- 健康养生要点
- 佩饰选择建议（基于五行）

注意：请结合用户的年龄特征（18岁、30岁、40岁、60岁），重点关注相应人生阶段的关键问题。分析要客观专业，建议要具体可行。`
          }
        ]
      })
    })

    if (!response.ok) {
      throw new Error('网络请求失败')
    }

    const data = await response.json()
    const result = data.choices[0].message.content
    parsedResult.value = parseResult(result)
  } catch (e) {
    error.value = '分析结果获取失败，请稍后重试'
    console.error('Analysis error:', e)
  } finally {
    loading.value = false
  }
}

onMounted(() => {
  fetchAnalysis()
})
</script>

