<template>
  <div class="user-info-form" v-if="!showResult">
    <div class="form-card">
      <h2 class="form-title">八字命理分析</h2>
      <form @submit.prevent="handleSubmit" class="form-container">
        <!-- 姓名 -->
        <div class="form-group">
          <label for="name" class="label">
            <span class="label-icon">👤</span>
            姓名
          </label>
          <input
            id="name"
            v-model="formData.name"
            type="text"
            required
            placeholder="请输入您的姓名"
            class="input"
          >
        </div>

        <!-- 性别 -->
        <div class="form-group">
          <label class="label">
            <span class="label-icon">⚤</span>
            性别
          </label>
          <div class="radio-group">
            <label class="radio-label">
              <input
                type="radio"
                v-model="formData.gender"
                value="male"
                required
              >
              <span class="radio-text">男</span>
            </label>
            <label class="radio-label">
              <input
                type="radio"
                v-model="formData.gender"
                value="female"
              >
              <span class="radio-text">女</span>
            </label>
          </div>
        </div>

        <!-- 出生日期 -->
        <div class="form-group">
          <label class="label">
            <span class="label-icon">📅</span>
            出生日期
            <span class="helper-text">请选择您的出生年月日</span>
          </label>
          <div class="date-selector">
            <!-- 年份选择 -->
            <select
              v-model="selectedYear"
              class="date-select year-select"
              required
              @change="updateBirthDate"
            >
              <option value="">请选择年份</option>
              <option 
                v-for="year in yearOptions" 
                :key="year.value" 
                :value="year.value"
              >
                {{ year.label }}
              </option>
            </select>

            <!-- 月份选择 -->
            <select
              v-model="selectedMonth"
              class="date-select month-select"
              required
              @change="updateBirthDate"
            >
              <option value="">月份</option>
              <option v-for="month in 12" :key="month" :value="month">
                {{month}}月
              </option>
            </select>

            <!-- 日期选择 -->
            <select
              v-model="selectedDay"
              class="date-select day-select"
              required
              @change="updateBirthDate"
            >
              <option value="">日期</option>
              <option v-for="day in daysInMonth" :key="day" :value="day">
                {{day}}日
              </option>
            </select>
          </div>
        </div>

        <!-- 出生时辰 -->
        <div class="form-group">
          <label class="label">
            <span class="label-icon">🕐</span>
            出生时辰
            <span class="helper-text">请选择您的出生时辰</span>
          </label>
          <div class="time-selector">
            <button
              type="button"
              v-for="time in timeOptions"
              :key="time"
              @click="selectTime(time)"
              :class="{'selected': time === selectedTime}"
              class="time-btn"
            >
              {{ time }}
            </button>
          </div>
        </div>

        <!-- 出生城市 -->
        <div class="form-group">
          <label for="birthCity" class="label">
            <span class="label-icon">🏙️</span>
            出生城市
          </label>
          <input
            id="birthCity"
            v-model="formData.birthCity"
            type="text"
            required
            placeholder="请输入出生城市"
            class="input"
          >
        </div>

        <!-- 婚姻状况 -->
        <div class="form-group">
          <label for="maritalStatus" class="label">
            <span class="label-icon">💑</span>
            婚姻状况
          </label>
          <select
            id="maritalStatus"
            v-model="formData.maritalStatus"
            required
            class="input"
          >
            <option value="">请选择</option>
            <option value="single">未婚</option>
            <option value="married">已婚</option>
            <option value="divorced">离异</option>
            <option value="widowed">丧偶</option>
          </select>
        </div>

        <button type="submit" class="submit-btn" :disabled="isLoading">
          <span class="btn-icon">{{ isLoading ? '🔮' : '✨' }}</span>
          {{ isLoading ? '命理分析中...' : '开始分析' }}
        </button>

        <div v-if="error" class="error-message">
          <span class="error-icon">⚠️</span>
          {{ error }}
        </div>
      </form>
    </div>
  </div>

  <ResultDisplay
    v-else
    :result="analysisResult"
    :user-info="formData"
    @back="showResult = false"
  />
</template>

<script setup>
import { ref, computed } from 'vue'
import dayjs from 'dayjs'
import ResultDisplay from './ResultDisplay.vue'

const formData = ref({
  name: '',
  gender: '',
  birthDate: '',
  birthTime: '',
  birthCity: '',
  maritalStatus: ''
})

const isLoading = ref(false)
const error = ref('')
const showResult = ref(false)
const analysisResult = ref('')
const selectedTime = ref('')

// 日期选择相关的响应式变量
const selectedYear = ref('1980')  // 默认选中1980年
const selectedMonth = ref('')
const selectedDay = ref('')

// 生成年份选项（以1980为中心，上下30年范围）
const yearOptions = computed(() => {
  const years = []
  const centerYear = 1980
  const range = 30  // 减小范围使1980年更容易定位到中间
  const startYear = centerYear - range
  const endYear = centerYear + range
  
  for (let year = startYear; year <= endYear; year++) {
    years.push({
      value: year,
      label: `${year}年`
    })
  }
  return years
})

// 计算当前选择的月份应该有多少天
const daysInMonth = computed(() => {
  if (!selectedYear.value || !selectedMonth.value) return 31
  const date = new Date(selectedYear.value, selectedMonth.value, 0)
  return date.getDate()
})

// 更新出生日期
const updateBirthDate = () => {
  if (selectedYear.value && selectedMonth.value && selectedDay.value) {
    const month = String(selectedMonth.value).padStart(2, '0')
    const day = String(selectedDay.value).padStart(2, '0')
    formData.value.birthDate = `${selectedYear.value}-${month}-${day}`
  } else {
    formData.value.birthDate = ''
  }
}

// 时辰选项
const timeOptions = [
  '子时 (23:00-1:00)',
  '丑时 (1:00-3:00)',
  '寅时 (3:00-5:00)',
  '卯时 (5:00-7:00)',
  '辰时 (7:00-9:00)',
  '巳时 (9:00-11:00)',
  '午时 (11:00-13:00)',
  '未时 (13:00-15:00)',
  '申时 (15:00-17:00)',
  '酉时 (17:00-19:00)',
  '戌时 (19:00-21:00)',
  '亥时 (21:00-23:00)'
]

// 选择时辰
const selectTime = (time) => {
  selectedTime.value = time
  // 提取时间范围的第一个小时作为表单值
  const hour = time.match(/\d{1,2}(?=:00)/)[0]
  formData.value.birthTime = `${hour.padStart(2, '0')}:00`
}

// 格式化日期显示
const formatDate = (date) => {
  if (!date) return ''
  return dayjs(date).format('YYYY年MM月DD日')
}

// 处理表单提交
const handleSubmit = async () => {
  try {
    if (!selectedTime.value) {
      error.value = '请选择出生时辰'
      return
    }

    isLoading.value = true
    error.value = ''

    // 模拟分析过程
    await new Promise(resolve => setTimeout(resolve, 2000))
    analysisResult.value = `尊敬的${formData.value.name}，您的八字命理分析结果已生成...
出生日期：${formatDate(formData.value.birthDate)}
出生时辰：${selectedTime.value}
出生城市：${formData.value.birthCity}`
    showResult.value = true
  } catch (e) {
    error.value = '分析过程出现错误，请稍后重试'
  } finally {
    isLoading.value = false
  }
}
</script>

